import React, { lazy, Suspense } from "react";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  useLocation,
} from "react-router-dom";
import { ThemeProvider } from "styled-components";
import { lightTheme, darkTheme } from "./theme";
import ScrollRestoration from "react-scroll-restoration";
import { useAppLoading } from './hooks/useAppLoading';
import Loading from "./components/Loading";

import history from "./routerHistory";
import "./App.css";

import useEagerConnect from "./hooks/useEagerConnnect";

const Home = lazy(() => import("./pages/Home"));
const Staking = lazy(() => import("./pages/Staking"));
const Projects = lazy(() => import("./pages/Projects"));
const PrivateSale = lazy(() => import("./pages/PrivateSale"));
const Vesting = lazy(() => import("./pages/Vesting"));
const ApplyIDO = lazy(() => import("./pages/ApplyIDO"));

export const ScrollTop = ({ children, location }) => {
  React.useEffect(() => window.scrollTo(0, 0), [location]);
  return children;
};

const App = () => {
  const { isAppLoading } = useAppLoading();
  useEagerConnect();
  return (
    <ThemeProvider theme={darkTheme}>
      <Router history={history}>
        <ScrollRestoration />
        <Suspense fallback={<Loading />}>
          <Switch>
            <Route exact path="/" component={Home} />
            {/* <Route exact path="/projects/" component={Projects} /> */}
            <Route exact path="/projects/" component={PrivateSale} />
            <Route exact path="/staking/" component={Staking} />
            <Route exact path="/vesting/" component={Vesting} />
            <Route exact path="/applyido/" component={ApplyIDO} />
          </Switch>
        </Suspense>
      </Router>
      {/* <Loading opened={isAppLoading} /> */}
    </ThemeProvider>
  );
};

export default App;
